document.addEventListener('DOMContentLoaded', function() {
    const downloadBtn = document.querySelector('.download-icon');
    
    if (downloadBtn) {
        downloadBtn.addEventListener('click', function(e) {
            e.preventDefault();
            
            const icon = this.querySelector('i');
            const originalIcon = icon.className;
            icon.className = 'fas fa-spinner fa-spin';
            
            // –û–ø—Ç–∏–º–∞–ª—å–Ω—ã–µ –Ω–∞—Å—Ç—Ä–æ–π–∫–∏ –¥–ª—è html2canvas
            const opt = {
                scale: 2, // –û–ø—Ç–∏–º–∞–ª—å–Ω—ã–π –±–∞–ª–∞–Ω—Å –∫–∞—á–µ—Å—Ç–≤–∞ –∏ —Ä–∞–∑–º–µ—Ä–∞
                useCORS: true,
                backgroundColor: '#ffffff',
                scrollX: 0,
                scrollY: 0,
                logging: false,
                allowTaint: true,
                letterRendering: true,
                ignoreElements: (element) => {
                    return element.classList.contains('edit-form') || 
                           element.classList.contains('download-icon');
                }
            };
            
            // –î–æ–±–∞–≤–ª—è–µ–º –≤—Ä–µ–º–µ–Ω–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è –ø–µ—á–∞—Ç–∏
            document.body.classList.add('printing');
            
            setTimeout(() => {
                html2canvas(document.querySelector('.resume'), opt).then(canvas => {
                    const { jsPDF } = window.jspdf;
                    const pdf = new jsPDF('p', 'mm', 'a4');
                    
                    // –†–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä—ã —Å —É—á–µ—Ç–æ–º –æ—Ç—Å—Ç—É–ø–æ–≤
                    const pageWidth = pdf.internal.pageSize.getWidth() - 25;
                    const pageHeight = pdf.internal.pageSize.getHeight() - 35;
                    
                    const imgWidth = pageWidth;
                    const imgHeight = (canvas.height * imgWidth) / canvas.width;
                    
                    // –î–æ–±–∞–≤–ª—è–µ–º –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Å –æ—Ç—Å—Ç—É–ø–∞–º–∏
                    pdf.addImage(canvas, 'PNG', 10, 10, imgWidth, imgHeight);
                
                
                    
                    pdf.save('Resume.pdf');
                    icon.className = originalIcon;
                    document.body.classList.remove('printing');
                }).catch(error => {
                    console.error('Error generating PDF:', error);
                    icon.className = originalIcon;
                    document.body.classList.remove('printing');
                });
            }, 300);
        });
    }
});